.work__filters {
    display: flex;
    align-items: center;
    column-gap: 1.875rem;
    margin-bottom: 2.5rem;

}
.work__item{
    cursor: pointer;
    font-weight: var(--font-bold);
    transition: .3s;
}
.work__item:hover{
    color: var(--first-color);
}
.work__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.875rem;
}
.work__card {
    position: relative;
    overflow: hidden;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    display: flex;
    height: 200px;
    width: 280px;
    
}
.work__mask {
    position: absolute;
    left: 0;
    top: 0;
    width:100%;
    height: 100%;
    background: #6c6ce5;
    transition: .3s;
    opacity: 0;

}
.work__img{
    object-fit: cover;
    min-width:300%;
    width: auto;
    height: 100%;
    
}
.work__card:hover .work__mask {
    opacity: 0.9;
}
